/**
 * External dependencies
 */
import classnames from "classnames";

/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import {
	useBlockProps,
	useInnerBlocksProps,
	InnerBlocks,
	BlockControls,
	InspectorControls,
	MediaReplaceFlow,
	withColors,
	__experimentalColorGradientSettingsDropdown as ColorGradientSettingsDropdown,
	__experimentalBlockAlignmentMatrixControl as BlockAlignmentMatrixControl,
	__experimentalUseMultipleOriginColorsAndGradients as useMultipleOriginColorsAndGradients,
	store as blockEditorStore,
} from "@wordpress/block-editor";

import {
	Button,
	ExternalLink,
	FocalPointPicker,
	PanelBody,
	PanelRow,
	TextareaControl,
} from "@wordpress/components";

import { useRef } from "@wordpress/element";

/**
 * WordPress Data API functions for managing application data.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-data/
 */
import { useSelect } from "@wordpress/data";

/**
 * Default snow color.
 */
const ALLOWED_MEDIA_TYPES = ["image"];

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import "./editor.scss";

/**
 * Allowed blocks constant is passed to InnerBlocks precisely as specified here.
 * The contents of the array should never change.
 * The array should contain the name of each block that is allowed.
 * In columns block, the only block we allow is 'core/column'.
 *
 * @constant
 * @type {string[]}
 */
const ALLOWED_BLOCKS = ["core/group"];

import {
	isContentPositionCenter,
	getPositionClassName,
	getRgbaColorObject,
	getMediaPosition,
} from "./utilities";

import SnowingCanvas from "./components/SnowingCanvas";
import Overlay from "./components/Overlay";
import BackgroundImage from "./components/BackgroundImage";

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @param {Object} props All props passed to this function.
 * @return {WPElement} Element to render.
 */
export function Edit(props) {
	const { attributes, setAttributes, snowColor, setSnowColor, clientId } =
		props;

	const blockClasses = classnames(
		{
			"has-custom-content-position": !isContentPositionCenter(
				attributes.contentPosition,
			),
		},
		getPositionClassName(attributes.contentPosition),
	);

	const blockProps = useBlockProps({
		className: blockClasses,
	});

	const hasChildBlock = useSelect(
		(select) => {
			const { getBlock } = select(blockEditorStore);
			const block = getBlock(clientId);

			// Directly return the check for child block existence.
			return block ? block.innerBlocks.length > 0 : false;
		},
		[clientId],
	);

	const innerBlocksProps = useInnerBlocksProps(
		{ className: "wallpaper-content" },
		{
			allowedBlocks: ALLOWED_BLOCKS,
			orientation: "vertical",
			renderAppender: hasChildBlock
				? () => null
				: InnerBlocks.ButtonBlockAppender,
		},
	);

	const colorGradientSettings = useMultipleOriginColorsAndGradients();

	let currentSnowColor = attributes.snowColorValue
		? attributes.snowColorValue
		: snowColor.color;

	const snowColorObject = getRgbaColorObject(currentSnowColor);

	const onSelectMedia = (newImage) => {
		setAttributes({
			backgroundImageUrl: newImage.url,
		});
	};

	const onClearMedia = () => {
		setAttributes({
			backgroundImageUrl: undefined,
		});
	};

	// Reference to the DOM element that to update.
	const backgroundImageRef = useRef();

	/**
	 * Imperatively set the focal point of the background image.
	 *
	 * To ensure changes to the focal point are reflected immediately,
	 * providing a live preview of how the image will look with the chosen focal point.
	 */
	const imperativeFocalPointPreview = (focalPoint) => {
		if (backgroundImageRef.current) {
			const style = backgroundImageRef.current.style;
			style.objectPosition = getMediaPosition(focalPoint);
		}
	};

	return (
		<>
			<InspectorControls group="color">
				<ColorGradientSettingsDropdown
					__experimentalIsRenderedInSidebar
					settings={[
						{
							colorValue: currentSnowColor,
							label: __("Snow"),
							onColorChange: (colorValue) => {
								setSnowColor(colorValue);
								setAttributes({ snowColorValue: colorValue });
							},
							isShownByDefault: true,
							resetAllFilter: () => {
								setSnowColor(undefined);
								setAttributes({ snowColorValue: undefined });
							},
							enableAlpha: true,
						},
					]}
					panelId={clientId}
					{...colorGradientSettings}
				/>
			</InspectorControls>
			<InspectorControls>
				{!!attributes.backgroundImageUrl && (
					<PanelBody title={__("Media settings")}>
						<FocalPointPicker
							__nextHasNoMarginBottom
							label={__("Focal point picker")}
							url={attributes.backgroundImageUrl}
							value={attributes.backgroundImageFocalPoint}
							onDragStart={imperativeFocalPointPreview}
							onDrag={imperativeFocalPointPreview}
							onChange={(newFocalPoint) =>
								setAttributes({
									backgroundImageFocalPoint: newFocalPoint,
								})
							}
						/>
						<TextareaControl
							__nextHasNoMarginBottom
							label={__("Alternative text")}
							value={attributes.backgroundImageAlt}
							onChange={(newAlt) =>
								setAttributes({ backgroundImageAlt: newAlt })
							}
							help={
								<>
									<ExternalLink href="https://www.w3.org/WAI/tutorials/images/decision-tree">
										{__("Describe the purpose of the image.")}
									</ExternalLink>
									<br />
									{__("Leave empty if decorative.")}
								</>
							}
						/>
						<PanelRow>
							<Button
								variant="secondary"
								className="block-library-cover__reset-button"
								onClick={onClearMedia}
							>
								{__("Clear Media")}
							</Button>
						</PanelRow>
					</PanelBody>
				)}
			</InspectorControls>
			<BlockControls group="block">
				<BlockAlignmentMatrixControl
					label={__("Change content position")}
					value={attributes.contentPosition}
					onChange={(nextPosition) =>
						setAttributes({
							contentPosition: nextPosition,
						})
					}
					isDisabled={!hasChildBlock}
				/>
			</BlockControls>
			<BlockControls group="other">
				<MediaReplaceFlow
					mediaURL={attributes.backgroundImageUrl}
					allowedTypes={ALLOWED_MEDIA_TYPES}
					accept="image/*"
					onSelect={onSelectMedia}
					name={
						!attributes.backgroundImageUrl
							? __("Add Background Image")
							: __("Replace")
					}
				/>
			</BlockControls>
			<div {...blockProps}>
				<BackgroundImage
					attributes={attributes}
					imageRef={backgroundImageRef}
				/>

				<Overlay
					attributes={attributes}
					setAttributes={setAttributes}
					clientId={clientId}
				/>
				<div className="wallpaper-canvas">
					<SnowingCanvas snowColor={snowColorObject} />
				</div>
				<div {...innerBlocksProps} />
			</div>
		</>
	);
}

const snowColorAttributes = {
	snowColor: "snow-color",
};

export default withColors(snowColorAttributes)(Edit);
