/**
 * External dependencies
 */
import classnames from "classnames";

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import {
	useBlockProps,
	useInnerBlocksProps,
	getColorClassName,
	__experimentalGetGradientClass,
} from "@wordpress/block-editor";

import {
	isContentPositionCenter,
	getPositionClassName,
	dimRatioToClass,
} from "./utilities";

import BackgroundImage from "./components/BackgroundImage";

/**
 * The save function defines the way in which the different attributes should
 * be combined into the final markup, which is then serialized by the block
 * editor into `post_content`.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#save
 *
 * @param {Object} props All props passed to this function.
 * @return {WPElement} Element to render.
 */
export default function save(props) {
	const { attributes } = props;
	const overlayColorClass = getColorClassName(
		"background-color",
		attributes.overlayColor,
	);
	const gradientClass = __experimentalGetGradientClass(attributes.gradient);
	const gradientValue = attributes.gradient || attributes.customGradient;

	const overlayStyles = {
		backgroundColor: !overlayColorClass
			? attributes.customOverlayColor
			: undefined,
		background: attributes.customGradient
			? attributes.customGradient
			: undefined,
	};

	const blockClasses = classnames(
		{
			"has-custom-content-position": !isContentPositionCenter(
				attributes.contentPosition,
			),
		},
		getPositionClassName(attributes.contentPosition),
	);

	const blockProps = useBlockProps.save({
		className: blockClasses,
	});

	return (
		<div {...blockProps}>
			<BackgroundImage attributes={attributes} />
			{!!attributes.dimRatio && (
				<span
					aria-hidden="true"
					className={classnames(
						"wallpaper-overlay",
						overlayColorClass,
						dimRatioToClass(attributes.dimRatio),
						{
							"has-background-dim": attributes.dimRatio !== undefined,
							"has-background-gradient": gradientValue,
							[gradientClass]: gradientClass,
						},
					)}
					style={overlayStyles}
				/>
			)}
			<div className="wallpaper-canvas"></div>
			<div
				{...useInnerBlocksProps.save({
					className: "wallpaper-content",
				})}
			/>
		</div>
	);
}
