import { getMediaPosition } from './../utilities';

export default function BackgroundImage( { attributes, imageRef } ) {
	// Make sure background image is present.
	if ( ! attributes.backgroundImageUrl ) {
		return;
	}

	// Create an object for conditionally applying the ref
	const backgroundImageRef = imageRef ? { ref: imageRef } : {};

	const backgroundImageStyle = {
		objectPosition: attributes.backgroundImageFocalPoint
			? getMediaPosition( attributes.backgroundImageFocalPoint )
			: undefined,
	};

	return (
		<img
			{ ...backgroundImageRef }
			className="wallpaper-image-background"
			src={ attributes.backgroundImageUrl }
			style={ backgroundImageStyle }
			alt={ attributes.backgroundImageAlt }
		/>
	);
}
